import { UploadFileInfo } from '@progress/kendo-react-upload';
import { clone } from '@progress/kendo-react-common';
import { BreadcrumbDataModel, DataModel, GridViewBtnGroup, SortingBtnGroup } from '../interfaces/FileManagerModels';

// .xlsx && .xls  => excel
// .jpg && .png   => picture
// .txt && .doc/x => text
// no extension   => folder
export const convertExtensionToIcon = (item: string | null | undefined) => {
  if (!item) { return null; }
  const extension: string = item.split('.')[1];

  switch (extension) {
    case 'xlsx': case 'xls':
      return {
        icon: 'k-i-file-data',
        type: 'Data'
      };
    case 'jpg': case 'png': case '.JPG':
      return {
        icon: 'k-i-file-image',
        type: 'Image'
      };
    case 'txt': case 'doc': case 'docx':
      return {
        icon: 'k-i-file-txt',
        type: 'Text'
      };
    default:
      return {
        icon: 'k-i-folder',
        type: 'Folder'
      };
  };
};

export const convertDateFormat = (date: Date | null, intl) => {
  return date ? intl.formatDate(date, 'd.MM.y  h:mm:ss aa  EEEE') : date;
};

const addNewData = (data: DataModel[] | null, intl) => {
  const newData = [] as DataModel[];

  if (data) {
    data.forEach(item => {
      const itemDate: Date = convertDateFormat(item.dateCreated || null, intl);
  
      newData.push({
        name: item.name,
        dateCreated: itemDate,
        size: item.size,
        items: item.items,
        icon: item.icon
      })
    });
  }
  return newData;
};

export const formatData = (data: DataModel[], intl) => {
  if (data) {
    return addNewData(data, intl);
  }
  return [];
};

export const convertToTreeData = (data: DataModel[], selectedItem: DataModel | null = null) => {
  const treeData = [] as DataModel[];

  data.forEach(item => {
    if (item.name && !item.name.includes('.')) {
      treeData.push({
        name: item.name,
        expanded: item.expanded,
        selected: selectedItem ? selectedItem.name === item.name : false,
        dateCreated: new Date(),
        icon: convertExtensionToIcon(undefined),
        items: item.items && item.items.length ? convertToTreeData(item.items, selectedItem) : []
      })
    }
  })
  return treeData;
};

export const clearData = (data: DataModel[], selectedItem: DataModel | null = null) => {
  const gridData = [] as DataModel[];

  data.forEach(item => {
    if (item.name) {
      gridData.push({
        ...item,
        items: item.items && item.items.length ? clearData(item.items, selectedItem) : []
      })
    }
  })
  return gridData;
};

export const searchTreeItem = (data: DataModel, curItem: DataModel | null) => {
  if (!curItem) { return data; }

  if (Array.isArray(data)) {
    for (let i = 0; i < data.length; i++) {
      let item: DataModel = searchTreeItem(data[i], curItem);
      if (item) {
        return item;
      }
    }
  } else if (typeof data === 'object') {
    if (data.name !== undefined && data.name === curItem.name) {
      return data;
    }
  }
  if (data.items !== undefined && data.items.length > 0) {
    return searchTreeItem(data.items, curItem);
  } else {
    return null;
  }
};

const deepCopy = (data: DataModel[]) => JSON.parse(JSON.stringify(data));

export const addDataItem = (data: DataModel | DataModel[], curItem: DataModel | null, files: UploadFileInfo[], intl) => {
  const updatedData: DataModel | DataModel[] = Array.isArray(data) ? deepCopy(data) : clone(data);
  const selectedItem = curItem ? clone(curItem) : {};

  if (!curItem && Array.isArray(updatedData)) {
    files.forEach(file => {
      updatedData.push({
        name: file.name, 
        dateCreated: convertDateFormat(new Date(), intl),
        size: file.size,
        icon: convertExtensionToIcon(file.extension)
      });
    });
    return { updatedData, selectedItem };
  }

  if (Array.isArray(updatedData)) {
    for (let i = 0; i < updatedData.length; i++) {
      let item: DataModel = searchTreeItem(updatedData[i], selectedItem);
      if (item) {
        files.forEach(file => {
          if (!item.items) {
            item.items = [];
          }

          item.items.push({
            name: file.name, 
            dateCreated: convertDateFormat(new Date(), intl),
            size: file.size,
            icon: convertExtensionToIcon(file.extension)
          });
          selectedItem.items = item.items;
        });
      }
    }
    return { updatedData, selectedItem };
  } else if (typeof updatedData === 'object') {
    if (updatedData.name !== undefined && updatedData.name === selectedItem.name) {
      return { updatedData, selectedItem };
    }
  }
  if (updatedData.items !== undefined && updatedData.items.length > 0) {
    return searchTreeItem(updatedData.items, selectedItem);
  } else {
    return null;
  }
};

let FOLDER_NAME_COUNTER = 0;
export const addFolder = (data: DataModel, curItem: DataModel | null, intl) => {
  const updatedData: DataModel | DataModel[] = Array.isArray(data) ? deepCopy(data) : clone(data);
  const selectedItem = curItem ? clone(curItem) : {};

  if (!curItem && Array.isArray(updatedData)) {
    const folderName = FOLDER_NAME_COUNTER ? `New Folder (${FOLDER_NAME_COUNTER})` : 'New Folder';
    FOLDER_NAME_COUNTER++;
    updatedData.push({
      name: folderName, 
      dateCreated: convertDateFormat(new Date(), intl),
      size: 124,
      icon: convertExtensionToIcon(undefined)
    });
    return updatedData;
  }

  if (Array.isArray(updatedData)) {
    for (let i = 0; i < updatedData.length; i++) {
      let item: DataModel = searchTreeItem(updatedData[i], selectedItem);
      if (item) {
        const folderName = FOLDER_NAME_COUNTER ? `New Folder (${FOLDER_NAME_COUNTER})` : 'New Folder';
        FOLDER_NAME_COUNTER++;
        if (!item.items) {
          item.items = [];
        }

        item.items.push({
          name: folderName,
          dateCreated: convertDateFormat(new Date(), intl),
          size: 124,
          icon: convertExtensionToIcon(undefined)
        });

        return updatedData;
      }
    }
  } else if (typeof data === 'object') {
    if (data.name !== undefined && data.name === selectedItem.name) {
      return data;
    }
  }
  if (data.items !== undefined && data.items.length > 0) {
    return searchTreeItem(data.items, selectedItem);
  } else {
    return null;
  }
};

export const searchTreePath = (data: DataModel, curItem: DataModel | null, path?: BreadcrumbDataModel) => {
  if (!curItem) { return; }

  if (Array.isArray(data)) {
    for (let i = 0; i < data.length; i++) {
      let item: DataModel = searchTreeItem(data[i], curItem, );
      if (item) {
        return item;
      }
    }
  } else if (typeof data === 'object') {
    if (data.name !== undefined && data.name === curItem.name) {
      return data;
    }
  }
  if (data.items !== undefined && data.items.length > 0) {
    return searchTreeItem(data.items, curItem);
  } else {
    return null;
  }
};

export const convertToBreadcrumbData = (data, selectedItem: DataModel | null, path: BreadcrumbDataModel[] = [{
    id: 'Home',
    name: 'Home',
    iconClass: 'k-i-home'
  }]) => {
  if (!selectedItem) {
    return path;
  }
 
  for (let i = 0; i < data.length; i++) {
    if (data[i].name === selectedItem.name) {
      path.push({
        id: data[i].name,
        name: data[i].name,
        items: data[i].items
      });
      
      return path;
    }
  }

  for (let i = 0; i < data.length; i++) {
      if (data[i].items?.length) {
        path.push({
          id: data[i].name,
          name: data[i].name,
          items: data[i].items
        });

        const newPath = convertToBreadcrumbData(data[i].items, selectedItem, path);
        return newPath;
      }
  };
};

export const toggleViewBtnGroup = (btnGroupState: GridViewBtnGroup, view: string) => {
  if (!btnGroupState.listView && view !== 'grid') {
    return { gridView: false, listView: true };
  }
  if (!btnGroupState.gridView && view !== 'list') {
    return { gridView: true, listView: false };
  }
  return btnGroupState;
};

export const toggleSortBtnGroup = (btnGroupState: SortingBtnGroup, curState: string) => {
  if (!btnGroupState.sortDesc && curState !== 'asc') {
    return { sortAsc: false, sortDesc: true };
  }
  if (!btnGroupState.sortAsc && curState !== 'desc') {
    return { sortAsc: true, sortDesc: false };
  }
  return btnGroupState;
};

export const formatBytes = (bytes: number, decimals: number = 2) => {
  if (bytes === 0) return '0 Bytes';

  const k: number = 1024;
  const dm: number = decimals < 0 ? 0 : decimals;
  const sizes: string[] = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];

  const i: number = Math.floor(Math.log(bytes) / Math.log(k));

  return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
};

export const editDataItem = (data, selectedItem: DataModel, newName: string) => {
  if (!data) { return; }

  for (let i = 0; i < data.length; i++) {
    for (let item = 0; item < data.length; item++) {
      if (data[item].name === selectedItem.name) {
        data[item].name = newName;
        data[item].dateModified = new Date();
        return data;
      }

      if (data[item].items) {
        const array = editDataItem(data[item].items, selectedItem, newName);
        if (array) {
          return array;
        }
      }
    }
  }
};

export const removeDataItem = (data, selectedItem: DataModel) => {
  for (let i = 0; i < data.length; i++) {
    for (let item = 0; item < data.length; item++) {
      if (data[item].name === selectedItem.name) {
        data[item] = {};
        return data;
      }

      if (data[item].items) {
        const array = removeDataItem(data[item].items, selectedItem);
        if (array) {
          return array;
        }
      }
    }
  }
};
