import * as React from 'react';
import { 
  Grid,
  GridCellProps,
  GridColumn as Column,
  GridKeyDownEvent,
  GridRowDoubleClickEvent,
  GridSelectionChangeEvent,
  GridSortChangeEvent,
  GRID_COL_INDEX_ATTRIBUTE
} from '@progress/kendo-react-grid';
import { useTableKeyboardNavigation } from "@progress/kendo-react-data-tools";
import { clearData, convertDateFormat, formatBytes } from '../helpers/helperMethods';
import { useInternationalization } from '@progress/kendo-react-intl';

const DateCreatedCell  = (props: GridCellProps) => {
  const intl = useInternationalization();

  const field: string = props.field || "";
  const date: Date = props.dataItem[field];
  const navigationAttributes = useTableKeyboardNavigation(props.id);

  return (
    <td
      {...{ [GRID_COL_INDEX_ATTRIBUTE]: props.columnIndex }}
      {...navigationAttributes}
    >
      {date === null ? "" : convertDateFormat(date, intl)}
    </td>
  );
};

const SizeCell = (props: GridCellProps) => {
  const field: string = props.field || "";
  const size: number = props.dataItem[field];
  const navigationAttributes = useTableKeyboardNavigation(props.id);

  return (
    <td
      {...{ [GRID_COL_INDEX_ATTRIBUTE]: props.columnIndex }}
      {...navigationAttributes}
    >
      {size === null ? "" : formatBytes(size)}
    </td>
  );
};

export const GridView = (props) => {
  const RowRender = (trElement, dataItem) => {
    const trProps = {
      onContextMenu: (event) => {
        event.preventDefault()
        handleContextMenu(event, dataItem);
      }
    }
    return React.cloneElement(trElement, { ...trProps }, trElement.props.children);
  };
  
  const handleOnSortChange = (event: GridSortChangeEvent) => {
    props.onSortChange({
      sort: event.sort,
      event: event
    });
  };

  const handleOnSelectionChange = (event: GridSelectionChangeEvent) => {
    props.onSelectionChange(event);
  };

  const handleOnKeyDown = (event: GridKeyDownEvent) => {
    props.onSelectionChange({
      pressedKey: true,
      event: event
    });
  };

  const handleDoubleClick = (event: GridRowDoubleClickEvent) => {
    props.onDoubleClick(event);
  };

  const handleContextMenu = (event: React.MouseEvent<HTMLDivElement, MouseEvent>, dataItem) => {
    props.onContextMenu({
      dataItem: dataItem.dataItem,
      event: event
    });
  };

  return (
    <Grid
      data={props.data ? clearData(props.data.data) : props.data}
      rowRender={RowRender}
      className={"k-filemanager-grid k-grid-display-block k-editable"}
      style={{ height: '100%' }}
      navigatable={true}
      sortable={{
        allowUnsort: false
      }}
      sort={props.sort}
      selectedField={'selected'}
      selectable={{ enabled: true, drag: true, mode: 'multiple' }}
      dataItemKey={props.dataItemKey}
      onSelectionChange={handleOnSelectionChange}
      onSortChange={handleOnSortChange}
      onKeyDown={handleOnKeyDown}
      onRowDoubleClick={handleDoubleClick}
      >
      <Column field="name" title="Name" />
      <Column field="dateCreated" title="Date Created" cell={DateCreatedCell} />
      <Column field="size" title="Size" cell={SizeCell} />
    </Grid>
  );
}
